/* App container */
.app {
  text-align: center;
  font-family: 'Arial', sans-serif;
  min-height: 100vh; /* Full viewport height */
  display: flex;
  flex-direction: column;
  align-items: center; /* Center horizontally */
  padding: 20px;
  background: #faf8ef;
}

/* Board container */
.board {
  display: grid;
  gap: 5px;
  margin: 20px auto 20 px auto;

  padding: 5px;
  border-radius: 5px;
  box-sizing: border-box;

  /* Flex centering for variable height */
  display: grid;
  justify-content: center;
  align-content: center;
}

/* Tile styles */
.tile {
  width: var(--tile-size);
  height: var(--tile-size);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: calc(0.4 * var(--tile-size));
  font-weight: bold;
  color: #776e65;
  background: #cdc1b4;
  border-radius: 3px;
  transition: background 0.2s, transform 0.2s;
}

.tile:hover {
  transform: scale(1.05);
}

.tile-0 { background: #cdc1b4; }
.tile-2 { background: #eee4da; }
.tile-4 { background: #ede0c8; }
.tile-8 { background: #f2b179; }
.tile-16 { background: #f59563; color: #f9f6f2; }
.tile-32 { background: #f67c5f; color: #f9f6f2; }
.tile-64 { background: #f65e3b; color: #f9f6f2; }
.tile-128 { background: #edcf72; color: #f9f6f2; }
.tile-256 { background: #edcc61; color: #f9f6f2; }
.tile-512 { background: #edc850; color: #f9f6f2; }
.tile-1024 { background: #edc53f; color: #f9f6f2; }
.tile-2048 { background: #edc22e; color: #f9f6f2; }

/* Messages */
.message {
  font-size: 1.5em;
  margin: 10px 0;
}

.win { color: green; }
.game-over { color: red; }

/* Buttons */
button {
  margin: 60px;
  padding: 10px 20px;
  font-size: 1em;
  cursor: pointer;
  background: #8f7a66;
  color: white;
  border: none;
  border-radius: 5px;
}

/* Config input */
.config {
  margin-top: 20px;
}

/* Instructions */
.instructions {
  margin-top: 20px;
  text-align: left;
  font-size: 0.9em;
  color: #776e65;
  max-width: 600px;
}
